@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

.header{grid-area:header;background:black;color:white;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-around;justify-content:space-around}

.character-card{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.character-card-content{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;border-radius:18px;position:relative;background-color:#fb2a4b;border:2px solid black;overflow:hidden;box-shadow:0px 0px 5px;transition:-webkit-transform 250ms ease-out;transition:transform 250ms ease-out;transition:transform 250ms ease-out, -webkit-transform 250ms ease-out;cursor:pointer}@media only screen and (max-width: 768px){.character-card-content{width:150px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;border-radius:18px;position:relative;background-color:#fb2a4b;border:2px solid black;overflow:hidden;box-shadow:0px 0px 5px;transition:-webkit-transform 250ms ease-out;transition:transform 250ms ease-out;transition:transform 250ms ease-out, -webkit-transform 250ms ease-out;cursor:pointer}.character-card-content-img{width:150px;height:150px;border-bottom:2px solid black}.character-card-content-title{-webkit-align-self:center;align-self:center;margin:8px 0px;font-size:16px}}.character-card-content:hover{-webkit-transform:translateY(-3px);transform:translateY(-3px)}.character-card-content-img{border-bottom:2px solid black}.character-card-content-title{-webkit-align-self:center;align-self:center;margin:8px 0px}

.custom-input{border-radius:8px;border:3px solid black;width:75%;height:40px;font-size:28px;padding-left:45px}.custom-input:focus{border-radius:8px;box-shadow:0 0 0 4px red;outline:none}.search-icon{position:absolute;padding:8px;border:2px solid black;border-radius:25%;margin:6px 6px}

.search-area{grid-area:search;margin-top:-20px;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;background:url(/matigimenez.github.io/static/media/Banner-Xavier.1e9cccda.jpg);background-size:cover;background-repeat:no-repeat;background-color:#441c4c;background-position:top center}.input-container{width:50%;text-align:center}.search-area-title{text-shadow:-3px -3px 0 #fff,-3px -2px 0 #fff,-3px -1px 0 #fff,-3px 0px 0 #fff,-3px 1px 0 #fff,-3px 2px 0 #fff,-3px 3px 0 #fff,-2px -3px 0 #fff,-2px -2px 0 #fff,-2px -1px 0 #fff,-2px 0px 0 #fff,-2px 1px 0 #fff,-2px 2px 0 #fff,-2px 3px 0 #fff,-1px -3px 0 #fff,-1px -2px 0 #fff,-1px -1px 0 #fff,-1px 0px 0 #fff,-1px 1px 0 #fff,-1px 2px 0 #fff,-1px 3px 0 #fff,0px -3px 0 #fff,0px -2px 0 #fff,0px -1px 0 #fff,0px 0px 0 #fff,0px 1px 0 #fff,0px 2px 0 #fff,0px 3px 0 #fff,1px -3px 0 #fff,1px -2px 0 #fff,1px -1px 0 #fff,1px 0px 0 #fff,1px 1px 0 #fff,1px 2px 0 #fff,1px 3px 0 #fff,2px -3px 0 #fff,2px -2px 0 #fff,2px -1px 0 #fff,2px 0px 0 #fff,2px 1px 0 #fff,2px 2px 0 #fff,2px 3px 0 #fff,3px -3px 0 #fff,3px -2px 0 #fff,3px -1px 0 #fff,3px 0px 0 #fff,3px 1px 0 #fff,3px 2px 0 #fff,3px 3px 0 #fff;margin-top:0}@media only screen and (max-width: 600px){.search-area-title{text-shadow:-2px -2px 0 #fff,-2px -1px 0 #fff,-2px 0px 0 #fff,-2px 1px 0 #fff,-2px 2px 0 #fff,-1px -2px 0 #fff,-1px -1px 0 #fff,-1px 0px 0 #fff,-1px 1px 0 #fff,-1px 2px 0 #fff,0px -2px 0 #fff,0px -1px 0 #fff,0px 0px 0 #fff,0px 1px 0 #fff,0px 2px 0 #fff,1px -2px 0 #fff,1px -1px 0 #fff,1px 0px 0 #fff,1px 1px 0 #fff,1px 2px 0 #fff,2px -2px 0 #fff,2px -1px 0 #fff,2px 0px 0 #fff,2px 1px 0 #fff,2px 2px 0 #fff;margin-top:0;font-size:19px}}@media only screen and (max-width: 768px){.search-area{grid-area:search;margin-top:-10px;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;background:url(/matigimenez.github.io/static/media/Banner-Xavier-mobile.5fc148d8.jpg);background-size:cover;background-repeat:no-repeat;background-color:#441c4c;background-position:center center}}@media only screen and (max-width: 992px){.search-area{grid-area:search;margin-top:-10px;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;background:url(/matigimenez.github.io/static/media/Banner-Xavier-mobile.5fc148d8.jpg);background-size:cover;background-repeat:no-repeat;background-color:#441c4c;background-position:center center}}

.dashboard{grid-area:main;display:grid;grid-template-columns:repeat(2, 1fr);grid-row-gap:10px;row-gap:10px}@media only screen and (min-width: 768px){.dashboard{grid-area:main;display:grid;grid-template-columns:repeat(2, 1fr);grid-row-gap:10px;row-gap:10px}}@media only screen and (min-width: 992px){.dashboard{grid-area:main;display:grid;grid-template-columns:repeat(3, 1fr);grid-row-gap:20px;row-gap:20px}}

.main-page{display:grid;width:100%;margin:0 auto;grid-template-columns:1fr;grid-template-rows:70px 200px auto 100px;grid-gap:10px;grid-template-areas:"header" "search" "main" "footer"}@media only screen and (min-width: 768px){.main-page{display:grid;width:100%;margin:0 auto;grid-template-columns:repeat(2, 1fr);grid-template-rows:70px 200px auto 100px;grid-gap:10px;grid-template-areas:"header header" "search search" "main main" "footer footer"}}@media only screen and (min-width: 992px){.main-page{display:grid;width:100%;margin:0 auto;grid-template-columns:repeat(3, 1fr);grid-template-rows:70px 200px auto 100px;grid-gap:20px;grid-template-areas:"header header header" "search search search" "main main main" "footer footer footer"}}

